import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { graphql } from 'gatsby'

// Utils
import { desktopVw } from '../../styles/utils'
import { getCurrentUser, isLoggedIn } from '../../utils/auth'

// Hooks
import useFetch from '../../hooks/useFetch'

import AccountWrapper from '../../components/Account/AccountWrapper'
import PageHeader from '../../components/Account/PageHeader'
import SpinningLoader from '../../components/SpinningLoader'
import BuyAgainThumbnail from '../../components/Account/BuyAgainThumbnail'

const ProductsList = styled.ul`
  position: relative;
  min-height: 60px;
  border-bottom: 1px solid ${props => props.theme.colors.greyLight};
  margin: 0 20px 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    min-height: ${desktopVw(60)};
    margin: ${desktopVw(50)} 0 0;
  }
`

const ProductItem = styled.li`
  margin-bottom: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: 0;
    margin-right: ${desktopVw(80)};
    padding-bottom: ${desktopVw(16)};
  }
`

const BuyAgain = ({ data }) => {
  const page = data.contentfulAccountPage

  const { fetchData, response, error, loading } = useFetch()

  const { email } = getCurrentUser()

  const url = `${process.env.GATSBY_ORDERS_API_ENDPOINT}/service/order?emailId=${email}&limit=50`

  const options = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  }

  useEffect(() => {
    if (isLoggedIn()) {
      if (email) fetchData(url, options)
    }
  }, [])

  const [products, setProducts] = useState([])
  const [noItems, setNoItems] = useState(false)
  useEffect(() => {
    if (!response) return

    if (response.status === 500) {
      setNoItems(true)
      return
    }

    const boughtProducts = response.filter(({ cancelled }) => !cancelled)
    const lineItems = boughtProducts.map(
      ({ shipments }) => shipments[0].lineItems,
    )

    setProducts(lineItems.flat())
  }, [response])

  return (
    <AccountWrapper
      heroImage={page.hero}
      menu={page.menuList}
      contact={page.contact}
      activeSlug={page.slug}
    >
      <PageHeader title={page.title.title} text={page.text.text} />
      <ProductsList>
        {loading && <SpinningLoader />}
        {products.length > 0 &&
          products.map(product => (
            <ProductItem key={product.id}>
              <BuyAgainThumbnail product={product} />
            </ProductItem>
          ))}
        {noItems && <div>No items to show</div>}
      </ProductsList>
    </AccountWrapper>
  )
}

export default BuyAgain

export const BuyAgainQuery = graphql`
  query BuyAgain($id: String!) {
    contentfulAccountPage(id: { eq: $id }) {
      id
      slug
      title {
        title
      }
      pageType
      hero {
        fluid(maxWidth: 3840) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      menuList {
        ...Link
      }
      text {
        text
      }
      contact {
        json
      }
    }
  }
`
