import React, { useState } from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { setCartOpen } from '../../actions'

import { desktopVw } from '../../styles/utils'

import useCheckoutLineItem from '../../hooks/useCheckoutLineItem'
import Button from '../Button'

const StyledBuyAgainThumbnail = styled.div`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
`

const LineItem = styled.div`
  display: flex;
  margin-bottom: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(5)};
  }
`

const Figure = styled.figure`
  margin-right: 12px;
  width: 84px;
  height: 108px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(12)};
    width: ${desktopVw(80)};
    height: ${desktopVw(104)};
  }
`

const Body = styled.div`
  flex: 1;
`

const Img = styled.img`
  object-fit: cover;
  width: 100%;
  height: 100%;
`

const TitleWrapper = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 7px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(5)};
  }
`

const Title = styled.h2`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 400;
  font-size: 16px;
  max-width: 80%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
  }
`

const Options = styled.div`
  margin-bottom: 10px;
  font-size: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-bottom: ${desktopVw(17)};
  }
`

const Option = styled.span`
  text-transform: uppercase;
  font-size: 10px;
  margin-right: 9px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-right: ${desktopVw(9)};
  }
`

const QuantityWrapper = styled.div`
  display: flex;
  align-items: center;
`

const QuantitySelector = styled.div`
  margin-right: 25px;
  display: flex;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(10)};
  }
`

const QuantityInput = styled.input`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 -1px;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid ${props => props.theme.colors.primary};
  width: 37px;
  height: 30px;
  font-size: 10px;
  font-family: ${props => props.theme.fonts.primary};
  text-align: center;
  background: transparent;
  border-radius: 0;
  -webkit-appearance: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(37)};
    height: ${desktopVw(25)};
    font-size: ${desktopVw(10)};
    border-color: ${props => props.theme.colors.greyLight};
  }
`

const QuantityButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  width: 33px;
  height: 30px;
  border: 1px solid ${props => props.theme.colors.primary};
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    border-color: ${props => props.theme.colors.greyLight};
    width: ${desktopVw(33)};
    height: ${desktopVw(25)};
    font-size: ${desktopVw(10)};
  }
`

const AddToCart = styled(Button)`
  background: ${props => props.theme.colors.primary};
  font-size: 12px;
  color: ${props => props.theme.colors.secondary};
  padding: 17px 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(12)} ${desktopVw(76)};
  }
`

const BuyAgainThumbnail = ({
  product: { variantId, title, image, price, variantTitle },
}) => {
  const [quantity, setQuantity] = useState(1)

  const dispatch = useDispatch()
  const { addVariantToCart } = useCheckoutLineItem()

  const addToCart = async () => {
    try {
      const storefrontVariantId = btoa(
        `gid://shopify/ProductVariant/${variantId}`,
      )

      await addVariantToCart({
        variantId: storefrontVariantId,
        quantity,
        customAttributes: [
          {
            key: 'createdAt',
            value: `${Date.now()}`,
          },
        ],
      })

      dispatch(setCartOpen(true))
    } catch (error) {
      console.log(error)
    }
  }

  return (
    <StyledBuyAgainThumbnail>
      <LineItem>
        <Figure>
          <Img src={image} />
        </Figure>
        <Body>
          <TitleWrapper>
            <Title>{title}</Title>
          </TitleWrapper>
          <Options>
            <Option>
              {variantTitle.replace('- /', '').replace('/ -', '')}
            </Option>
            <Option>{`Price: $${price}`}</Option>
          </Options>
          <QuantityWrapper>
            <QuantitySelector>
              <QuantityButton
                type='button'
                onClick={() => {
                  quantity > 1 && setQuantity(quantity - 1)
                }}
              >
                -
              </QuantityButton>
              <QuantityInput
                type='text'
                readOnly
                value={quantity < 10 ? `0${quantity}` : quantity}
              />
              <QuantityButton
                type='button'
                onClick={() => setQuantity(quantity + 1)}
              >
                +
              </QuantityButton>
            </QuantitySelector>
          </QuantityWrapper>
        </Body>
      </LineItem>
      <AddToCart onClick={addToCart}>Add to Cart</AddToCart>
    </StyledBuyAgainThumbnail>
  )
}

export default BuyAgainThumbnail
